'use strict';

// MODULES //

import resolve from '@stdlib/ndarray-base-dtype-resolve-str';
import table from './table.js';


// VARIABLES //

var TABLE;


// MAIN //

/**
* Returns the description for a provided data type.
*
* @param {*} [dtype] - data type value
* @returns {(Object|string|null)} description(s)
*
* @example
* var obj = dtypeDesc();
* // returns {...}
*
* @example
* var desc = dtypeDesc( 'float64' );
* // returns '...'
*
* desc = dtypeDesc( 'generic' );
* // returns '...'
*/
function dtypeDesc( dtype ) {
	if ( arguments.length === 0 ) {
		return table();
	}
	if ( TABLE === void 0 ) {
		TABLE = table();
	}
	return TABLE[ resolve( dtype ) ] || null;
}


// EXPORTS //

export default dtypeDesc;
